import React from 'react';
import {inject, observer} from 'mobx-react';
import {Form, Button, message, Popconfirm} from 'antd';
import * as  mobx from 'mobx';
import EditeFormVisit from './EditeFormVisit';
import EditeFormMonthlySharing from './EditeFormMonthlySharing';
import EditeFormSummary from './EditeFormSummary';
import EditeFormReplay from './EditeFormReplay';
import EditeFormNews from './EditeFormNews';
import EditeFormWeekreport from './EditeFormWeekreport';
import EditeFormFactoryreport from './EditeFormFactoryreport';

@inject('appStore')
@observer
class EditeVisit extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			loading: false
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		let _this = this;
		let {pathname} = _this.props;
		if (pathname&&pathname.includes("/digital/draft")&&_this.store.draftId!="") {
			// 
			_this.timer = setInterval(() => {
				//  30s
				_this.child.saveAuto(0);
			}, 30000);
		}
	}

	componentWillUnmount () {
		// 
		this.timer&&clearInterval(this.timer);
		this.timer = null;
	}

	saveDraft = () => {
		this.child.saveDraft();
	}

	handleSubmit = ()=> {
		this.child.handleSubmit();
	}

	onClose = () => {
		this.child.onClose();
	}

	onRef = (ref) => {
		this.child = ref
	}

	updateState = (type, data) => {
		this.setState({
			[type]: data
		});
	}

	render() {
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 3},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 20},
			},
		};
		let {detailData, temType} = this.props;
		const {loading} = this.state;

		return (
			<Form {...formItemLayout}>
				{temType=="visit"? <EditeFormVisit
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				{temType=="monthlySharing"? <EditeFormMonthlySharing
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				{temType=="summary"? <EditeFormSummary
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				{temType=="replay"? <EditeFormReplay
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				{temType=="news"? <EditeFormNews
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				{temType=="weekreport"? <EditeFormWeekreport
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				{temType=="factoryreport"? <EditeFormFactoryreport
					onRef={this.onRef}
					store={this.store}
					detailData={mobx.toJS(detailData)}
					handleDraftRoute={this.props.handleDraftRoute}
					handleRoute={this.props.handleRoute}
					getTable={this.props.getTable}
					onCancle={this.props.onCancle}
					updateState={this.updateState}
					pathname={this.props.pathname}
				/>:null}
				<Form.Item
					wrapperCol={{
						xs: {span: 24},
						sm: {span: 24}
					}}
                    className="clearfix tr mt20 pl20 pr20 fixed_bottom"
					style={{
						width: this.props.appStore.collapsed?"calc(100% - 470px)":" calc(100% - 590px)"
					}}
				>
					{
						this.props.isWeekShow!=undefined&&this.props.isWeekShow==false?null:<Button className="fl"
						                                                                            shape="round"
						                                                                            style={{
							                                                                            position: 'relative',
							                                                                            top: '4px'
						                                                                            }}
						                                                                            type="primary"
						                                                                            onClick={this.saveDraft}>
							
						</Button>
					}
					<Popconfirm placement="top" title="?" onConfirm={() => this.onClose()}>
						<Button shape="round" style={{marginRight: 8}}>
							
						</Button>
					</Popconfirm>
					<Button shape="round"
					        loading={loading}
					        onClick={this.handleSubmit}
					        type="primary">
						
					</Button>
				</Form.Item>
			</Form>
		);
	}
}
export default EditeVisit;



