import React from 'react';
import { Comment, Avatar, Form, Button, Input,Icon, Tooltip, message } from 'antd';
import moment from 'moment';
import {inject, observer} from 'mobx-react';
import * as  mobx from 'mobx';
import * as utils from "@/utils/utils";

const { TextArea } = Input;
const Editor = ({handleClick, onChange, onSubmit, submitting, value }) => (
	<div>
		<Form.Item>
			<TextArea rows={4} onChange={onChange} value={value} placeholder=""/>
		</Form.Item>
		<div  style={{
			width: '100%',
			display: 'flex',
			justifyContent: 'space-between'
		}}>
			<Button type="link" onClick={handleClick} style={{fontSize: '12px'}}>
				
			</Button>
			<Button htmlType="submit" loading={submitting} onClick={value==null||value==""?null:onSubmit} type="primary">
				
			</Button>
		</div>
	</div>
);
const Editor2 = ({ onChange, onSubmit, submitting, value }) => (
	<div className="mt20">
		<Form.Item>
			<TextArea rows={4} onChange={onChange} value={value} placeholder=""/>
		</Form.Item>
		<Form.Item className="tr">
			<Button htmlType="submit" loading={submitting} onClick={value==null||value==""?null:onSubmit} type="primary">
				
			</Button>
		</Form.Item>
	</div>
);

// 
class ExampleComment extends React.Component {
	state = {
		submitting: false,
		value: '',
		visible: false,
	};
	handleSubmit = () => {
		if (!this.state.value) {
			return;
		}
		this.setState({
			submitting: true,
		});
		setTimeout(() => {
			const { addComment, selestedId, id, userId, pid} = this.props;
			const params = {
				text: this.state.value,
				mainId: selestedId,
				pid: pid==null?id:pid,
				pUserId: userId
			}
			addComment(params, ()=>{
               this.reset();
			});
		}, 1000);
	};
	handleChange = e => {
		this.setState({
			value: e.target.value,
		});
	};
	handleClick = () => {
		this.setState({
			visible: !this.state.visible
		});
	}
	reset = () => {
		this.setState({
			submitting: false,
			value: '',
			visible: false,
		});
	}
	// 
	onDel = () => {
		const {delComment, id} = this.props;
		const params = {
			id: id
		}
		delComment(params, ()=>{
			this.reset();
		});
	}
	render() {
		const {
			value,
			submitting,
			visible
		} = this.state;
		const {text, userName, createTime, children,pid, parentUserName} = this.props;
		const username = localStorage.getItem("username");
		// 
		let isShow = userName==username;
		return (<Comment
				className={pid==null?"comment_normal":"comment_normal comment_active"}
				actions={[
					<span key="comment-nested-delete" className="comment-nested">
						{createTime==null?"2020-08-04":moment(createTime).format("YYYY-MM-DD HH:mm:ss")}
					    <span className={isShow?"comment-nested-delete":"hidden"} onClick={this.onDel}>&nbsp;&nbsp;</span>
					</span>,
					<span key="comment-nested-reply-to" style={{color: "#1890ff"}} onClick={this.handleClick}></span>]}
				author={<a>{userName}</a>}
				avatar={
					<Avatar
						src="./images/logo_new.png"
						alt="Han Solo"
					/>
				}
				content={
					<p>
						{parentUserName==null||parentUserName==""?null:`${parentUserName}:`} {text}
					</p>
				}
			>
				{visible?<Editor
					onChange={this.handleChange}
					onSubmit={this.handleSubmit}
					submitting={submitting}
					value={value}
					handleClick={this.handleClick}
				/>:null}
				{children}
			</Comment>
		);
	}
}

@observer
class DigitalComment extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			comments: [],
			submitting: false,
			value: '',
		};
	}

	componentWillReceiveProps(nextProps) {
	}

	componentDidMount() {

	}

	handleSubmit = () => {
		if (!this.state.value) {
			return;
		}

		this.setState({
			submitting: true,
		});

		setTimeout(() => {
			const params = {
				text: this.state.value,
				mainId: this.store.selestedId,
				pid: null
			}
			this.store.addComment(params).then((res)=>{
				if (res) {
					message.success("");
					this.setState({
						submitting: false,
						value: ""
					});
					// 
					this.store.getCommentsByJournalId();
				}
			});
		}, 1000);
	};

	handleChange = e => {
		this.setState({
			value: e.target.value,
		});
	};

	clearState = () => {
		this.setState({
			submitting: false,
			value: ''
		});
	}

	// 
	addComment = (params, callback) => {
		 this.store.addComment(params).then((res)=>{
			if (res) {
				message.success("");
				// 
				this.store.getCommentsByJournalId();
				if(callback) callback();
			}
		});
	}

	// 
	delComment = (params, callback) => {
		this.store.delComment(params).then((res)=>{
			if (res) {
				message.success("");
				// 
				this.store.getCommentsByJournalId();
				if(callback) callback();
			}
		});
	}

	renderTreeNodes = data =>
		data.map(item => {
			if (item.children) {
				return (<ExampleComment key={item.id} {...item} addComment={this.addComment} delComment={this.delComment} selestedId={this.store.selestedId}>
						{this.renderTreeNodes(item.children)}
					</ExampleComment>
				);
			}
			return <ExampleComment key={item.id} {...item} addComment={this.addComment} delComment={this.delComment} selestedId={this.store.selestedId}/>;
		});

	// 
	handleZan = () =>{
		this.store.addOrDelLike()
		.then((res)=>{
			if (res==true) {
				message.success("");
				// 
				this.store.getLikeUsersByJournalId();
			}
		});
	}

	render() {
		const { submitting, value } = this.state;
		const {getLikeUsers, getComments, readUsers} = this.store;
		const treeData = utils.formatList(mobx.toJS(getComments));
		const username = localStorage.getItem('username');
		const isLike = mobx.toJS(getLikeUsers).filter(item=>item.userName==username).length>0?true:false;
		return (
			<div className="detail_form_bold" style={{paddingLeft: '14px'}}>
				<div className="commentlist_title" style={{textAlign: 'left'}}>
					<Tooltip title="">
						{isLike?<Icon theme="filled" type="like" style={{marginLeft: '0'}} className="commentlist_icon pointer mr20" onClick={this.handleZan}/>:<Icon type="like" style={{marginLeft: '0'}} className="commentlist_icon pointer mr20" onClick={this.handleZan}/>}
					</Tooltip>
					{mobx.toJS(getLikeUsers).length>0?mobx.toJS(getLikeUsers).map((item)=>item.userName).join(""):null}
				</div>
				<div className="commentlist_title" style={{textAlign: 'left'}}>
					<Tooltip title="">
						<Icon theme="filled" type="eye" style={{marginLeft: '0'}} className="commentlist_icon pointer mr20" />
					</Tooltip>
					{mobx.toJS(readUsers).length>0?mobx.toJS(readUsers).map((item)=>item.userName).join(""):null}
				</div>
				<div id="commentlist" className="detail_form_bold_title" style={{textAlign: 'left'}}></div>
				<Editor2
					onChange={this.handleChange}
					onSubmit={this.handleSubmit}
					submitting={submitting}
					value={value}
				/>
				{this.renderTreeNodes(treeData)}
			</div>
		);
	}
}

export default DigitalComment;



