import React from 'react';
import {message, Spin, Button,Modal, Checkbox, Row, Col} from 'antd';
import {inject, observer} from 'mobx-react'
import * as mobx from 'mobx';
import EditableTagEmail2 from "@/components/digitalOrg/EditableTagEmail2";
import PersonAdd2 from "@/components/worktile/PersonAdd";
import {downloadFile} from "@/utils/download";

@inject('appStore')
@observer
class CommonEmail2 extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			tip: "...",
			isLoading: false,
			emails: [], // 
			userIds:[], // ,
			visible: false,
			checks: [],
			ccEmails: [], // 
			ccUserIds: [], // 
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
	}

	// 3pdf
	getReportUrl = (id) => {
		let _this = this;
		_this.setState({
			isLoading: true
		});
		_this.props.updateState("loading", true);
		const detailData = mobx.toJS(_this.props.detailData);
		const journalMain = detailData.journalMain || {};
		const pdfName = journalMain.title;

		_this.timer22 = setInterval(() => {
			_this.store.getDownloadUrl(id).then((res) => {
				const {url} = res;
				if (url !== null && url !== "") {
					_this.setState({
						isLoading: false
					});
					_this.props.updateState("loading", false);
					downloadFile(url, pdfName);  // pdf
					clearInterval(_this.timer22);
				} else {
					if (res.code == 2) {
						_this.setState({
							isLoading: false
						});
						_this.props.updateState("loading", false);
						clearInterval(_this.timer22);
					}
				}
			});
		}, 3000);
	}

	// word
	creatWord = (type) =>{
		const {detailData} = this.props;
		const param = mobx.toJS(detailData);
		if (type==1) {
			this.store.wordDownload(param)
			.then((res) => {
				this.getReportUrl(res);
			});
		} else {
			this.store.pdfDownload(param)
			.then((res) => {
				this.getReportUrl(res);
			});
		}
	}

	// 
	sendReport = () => {
		let _this = this;
		const {emails, userIds, checks, ccEmails, ccUserIds} = _this.state;
		const isemailUserIds = userIds == null ? [] : userIds;
		const isemails = emails == null ? [] : emails;
		// 
		if (isemailUserIds.length == 0 && isemails.length == 0) {
			message.warn("");
			return false;
		}
		const {detailData} = this.props;
		const params = {
			journal: mobx.toJS(detailData),
			emails: emails,
			userIds: userIds == null ? [] : userIds.map(item => item.userId),
			attType: checks.map(item=>typeof item =="string"?Number(item):item),
			ccEmails,
			ccUserIds: ccUserIds == null ? [] : ccUserIds.map(item => item.userId),
		}
		this.store.sendEmail(params)
			.then((res) => {
				if (res == true) {
					message.success("");
					this.reset();
				}
			});
	}

	updateState = (type, data) => {
		this.setState({
			[type]: Object.assign([], data)
		});
	}

	reset = () => {
		this.setState({
			emails: [], // 
			userIds:[], // 
			isLoading: false,
			visible: false,
			checks: [],
			ccEmails: [], // 
			ccUserIds: [], // 
		});
	}

	handleOpen = () => {
		this.setState({
			visible: true
		});
	}

	handleOk = () => {
		this.setState({
			visible: false
		});
	}

	onChange = (checkedValues) =>{
		this.setState({
			checks: checkedValues
		})
	}

	render() {
		const {tip, isLoading, emails, userIds, checks, ccEmails, ccUserIds} = this.state;
		const options = [
			{ label: 'Word', value: '0' },
			{ label: 'PDF', value: '1' }
		];

		return (
			<div>
				<ul>
					<li style={{
						listStyle: 'none',
						textAlign: 'left'
					}}><Button type="link" onClick={isLoading?null:this.creatWord.bind(this, "1")}>Word</Button></li>
					<li style={{
						listStyle: 'none',
						textAlign: 'left'
					}}><Button type="link" onClick={isLoading?null:this.creatWord.bind(this, "2")}>PDF</Button></li>
					<li style={{
						listStyle: 'none',
						textAlign: 'left'
					}}>
						<Button type="link" onClick={isLoading?null:this.handleOpen}></Button>
						<Checkbox.Group
							value={checks}
							options={options}
							onChange={this.onChange}
						/>
					</li>
				</ul>
				{/**/}
				<Modal
					maskClosable={false}
					width="600px"
					zIndex={"1066"}
					visible={this.state.visible}
					onCancel={this.reset}
					footer={null}
				>
					<Row>
						<Col span={24}>
							<span style={{marginRight:'5px'}}>
								
							</span>
							<EditableTagEmail2
								data={emails}
								updateState={this.updateState.bind(this, 'emails')}
								title={""}
							/>
							<PersonAdd2
								data={userIds}
								updatePerson={this.updateState.bind(this, 'userIds')}
								disabled={false}
								title={""}
							/>
						</Col>
						<Col span={24} className="mt15">
							<span style={{marginRight:'5px'}}>
								
							</span>
							<EditableTagEmail2
								data={ccEmails}
								updateState={this.updateState.bind(this, 'ccEmails')}
								title={""}
							/>
							<PersonAdd2
								data={ccUserIds}
								updatePerson={this.updateState.bind(this, 'ccUserIds')}
								disabled={false}
								title={""}
							/>
						</Col>
					</Row>
					<div className="w tc mt15">
						<Button type="primary" onClick={this.sendReport}></Button>
					</div>
				</Modal>
			</div>
		);
	}
}

export default CommonEmail2;



