import React from 'react';
import {inject, observer} from 'mobx-react';
import {Form, Input, Icon, Row, Col, Button, DatePicker, message, Popconfirm, InputNumber, Spin, Checkbox} from 'antd';
import moment from 'moment';
import BraftEditor from 'braft-editor';
import 'braft-editor/dist/index.css';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import PersonAdd2 from "@/components/worktile/PersonAdd";
import EditableTagGroup from "@/components/digitalOrg/EditableTagGroup";
import {showText} from "@/utils/utils";
import * as  mobx from 'mobx';
import {Base64} from 'js-base64';
import MyUpload from "@/components/common/MyUploadNew";
//import EditableTagEmail from "@/components/digitalOrg/EditableTagEmail";
import {v4 as uuidv4} from 'uuid';
import {downloadFile} from 'utils/download';
// import Audio from './Audio';
import AudioRecorder from './AudioRecorder';
import emitter from "@/utils/events";
const {TextArea} = Input;
import { ContentUtils } from 'braft-utils';
import MyUploadOne from "@/components/common/MyUploadOne";
import EditableTagEmail2 from "@/components/digitalOrg/EditableTagEmail2";
import ReactKindeditor from "@/components/common/ReactKindeditor";


@inject('appStore')
@observer
class EditeMeeting extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			assignments: [],
			tags: [],
			title: "",
			time: moment().format("YYYY-MM-DD"),
			attachments: [],
			people: "",
			target: "",
			content: "",
			conclusion: "",
			place: "",
			hours: "",
			isLoading: false,
			tip: "...",
			loading: false,
			detailData: {},
			emails: [], // 
			userIds:[], // 
			audio: [], // 
			audioStatus: "", // 
			checks: [],
			ccEmails: [],
			ccUserIds: [],
		}
	}

	componentWillReceiveProps(nextProps) {
		const {detailData} = nextProps;
		if (JSON.stringify(detailData) != JSON.stringify(this.props.detailData)) {

			const journalSub = Base64.decode(detailData.journalSub == undefined ? "" : detailData.journalSub) || {};
			const sub = typeof journalSub == "string" ? JSON.parse(journalSub) : {};

			const assignments = detailData.assignments == null ? [] : detailData.assignments;
			const journalMain = detailData.journalMain || {};
			const atts = detailData.atts == null ? [] : detailData.atts;
			const audio = detailData.audio == null ? [] : detailData.audio;

			this.setState({
				detailData: detailData,
				title: sub.title,
				time: sub.time==null||sub.time==0?moment().format("YYYY-MM-DD"):moment(sub.time).format("YYYY-MM-DD"),
				people: sub.people,
				target: sub.target==null||sub.target==""?"":sub.target,
				content: sub.content==null||sub.content==""?"":sub.content,
				conclusion: sub.conclusion==null||sub.conclusion==""?"":sub.conclusion,
				place: sub.place,
				hours: sub.hours,
				assignments: assignments,
				tags: journalMain.tags==null?[]:journalMain.tags.map(item=>item.name),
				attachments: atts==null?[]:atts.map((vitem, vindex) => {
					return Object.assign({}, {
						uid: vitem.id==null||vitem.id==""?"!" + uuidv4():vitem.id,
						name: vitem.fileName,
						status: 'done',
						url: vitem.filePath,
						fileId: vitem.id==null?"!" + uuidv4():vitem.id,
						fileName:vitem.fileName,
						filePath: vitem.filePath,
						id: vitem.id==null?"!" + uuidv4():vitem.id,
					})
				}),
				emails: [], // 
				userIds:[], // 
				audio: audio,
				checks: [],
				ccEmails: [],
				ccUserIds: [],
			}, ()=>{
				// 
				this.props.form.setFieldsValue({
					target: sub.target==null||sub.target==""?"":sub.target,
					content: sub.content==null||sub.content==""?"":sub.content,
					conclusion: sub.conclusion==null||sub.conclusion==""?"":sub.conclusion
				});
			});
		}
	}

	componentDidMount() {
		let _this = this;
		let {pathname, detailData} = _this.props;
		// 
		if (Object.keys(detailData).length>0) {
			_this.init(detailData);
		}
		_this.props.onRef(this);
		if (pathname&&pathname.includes("/digital/draft")&&_this.store.draftId!="") {
			// 
			_this.timer = setInterval(() => {
				//  30s
				_this.saveAuto(0);
			}, 30000);
		}
	}

	componentWillUnmount () {
		// 
		this.timer&&clearInterval(this.timer);
		this.timer = null;
	}

	updateState = (type, data) => {
		this.setState({
			[type]: Object.assign([], data)
		});
	}

	handleChange = (type, e) => {
		this.setState({
			[type]: e.target.value
		});
	}

	handleNumChange = (type, e) => {
		this.setState({
			[type]: e
		});
	}

	onDateChange = (date, dateString) => {
		this.setState({
			time: dateString
		});
	}

	handleBChange = (type, editorState) => {
		this.setState({[type]: editorState});
	}

	// 
	remove = (index) => {
		let customList = JSON.parse(JSON.stringify(this.state.assignments));
		customList.splice(index, 1);
		this.setState({
			assignments: Object.assign([], customList)
		});
	};

	handleAdd = () => {
		let customList = JSON.parse(JSON.stringify(this.state.assignments));
		if (customList.some((item) => {
			return item.name == "" || item.name == null;
		})) {
			message.warn("");
			return false;
		}
		if (customList.some((item) => {
			return item.userInformations.length == 0
		})) {
			message.warn("");
			return false;
		}
		this.setState({
			assignments: [...customList, {
				"name": "",
				"userInformations": []
			}]
		});
	}

	// 
	updatePerson = (index, userIds) => {
		let fields = JSON.parse(JSON.stringify(this.state.assignments));
		fields[index].userInformations = Object.assign([], userIds)
		this.setState({
			assignments: fields
		});
	}

	onChange = (index, e) => {
		let fields = JSON.parse(JSON.stringify(this.state.assignments));
		fields[index].name = e.target.value;
		this.setState({
			assignments: fields
		});
	}

	// 
	handleAttment = (type, params) => {
		// 
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});
		this.setState({
			[type]: Object.assign([], arr)
		});
	}

	// 
	saveAuto = (type) => {
		const {assignments, tags, title, time, attachments, people, target, content, conclusion, place, hours, audio, audioStatus} = this.state;
		if (type==1) {  // 
			// 
			if (this.props.appStore.journal==true) {
				message.warn("/");
				return false;
			}
			// 
			if (audioStatus=="recording") {
				message.warn("");
				return false;
			}
		}
		const temList = mobx.toJS(this.store.temList);
		let attcs = JSON.parse(JSON.stringify(attachments));

		const numTime = moment(time).valueOf(); // 
		const newTime = moment(numTime).format("YYYYMMDD");
		let journalSub = {
			title,
			time: numTime,
			people,
			place,
			hours,
			target: target,
			content: content,
			conclusion: conclusion
		};

		// 
		const params = {
			id: this.store.draftId,
			journalMain: {
				id: "",
				title: newTime + "--" + title,
				userId: localStorage.getItem('userid'),
				userName: localStorage.getItem('username'),
				tags: tags.map(item => Object.assign({}, {id: "", name: item})),
				type: temList.type
			},
			journalSub: Base64.encode(JSON.stringify(journalSub)),
			assignments: assignments.filter((item)=>{
				return !((item.name == "" || item.name == null)&&(item.userInformations.length == 0))
			}),
			atts: attcs == null ? [] : attcs.map(item => Object.assign({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id
			})),
			audio: audio
		}

		this.store.saveJournalByType({
			journal: params,
			saveType: type
		}).then((res)=>{
			if(res) {
				this.store.draftId = res.id;
			}
			if (res&&type==1) {
				message.success("");
				// 
				this.reset();

				const {pathname} = this.props;
				if (pathname == "/digital/draft") {  // 
					emitter.emit("draft", "3");
				} else {
					// 
					if (this.props.handleDraftRoute) {
						this.props.handleDraftRoute();
					}
				}

			}
		});
	}

	// 
	saveDraft = () => {
		this.saveAuto(1);
	}

	// 
	handleSubmit = () => {
		const {assignments, tags, attachments, audio,target, content, conclusion, audioStatus} = this.state;
		// 
		if (this.props.appStore.journal==true) {
			message.warn("/");
			return false;
		}
		// 
		if (audioStatus=="recording") {
			message.warn("");
			return false;
		}

		// 
		if (this.props.appStore.journalAudio==true) {
			message.warn(", ");
			return false;
		}

		const temList = mobx.toJS(this.store.temList);
		let attcs = JSON.parse(JSON.stringify(attachments));

		// 
		if (Array.isArray(tags) && tags.length == 0) {
			message.warn("");
			return false;
		}

		// 
		if (target=="") {
			message.warn("");
			return false;
		}
		if (content=="") {
			message.warn("");
			return false;
		}
		if (conclusion=="") {
			message.warn("");
			return false;
		}

		// 
		if (!assignments.some((item) => {
			return (item.name == "" || item.name == null)&&(item.userInformations.length == 0)
		})) {
			if (assignments.some((item) => {
				return item.name == "" || item.name == null;
			})) {
				message.warn("");
				return false;
			}
			if (assignments.some((item) => {
				return item.userInformations.length == 0
			})) {
				message.warn("");
				return false;
			}
		}

		const { form } = this.props;
		// 
		form.validateFields((err, values) => {
			if (err) return;
			if (!err) {
				const {title, time, people, place,hours} = values;

				const numTime = moment(time).valueOf(); // 
				const newTime = moment(numTime).format("YYYYMMDD");
				let journalSub = {
					title,
					time: numTime,
					people,
					place,
					hours,
					target: target,
					content: content,
					conclusion: conclusion
				};

				// 
				const params = {
					id: this.store.draftId,
					journalMain: {
						id: "",
						title: newTime + `-${temList.name}-` + title,
						userId: localStorage.getItem('userid'),
						userName: localStorage.getItem('username'),
						tags: tags.map(item => Object.assign({}, {id: "", name: item})),
						type: temList.type
					},
					journalSub: Base64.encode(JSON.stringify(journalSub)),
					assignments: assignments.filter((item)=>{
						return !((item.name == "" || item.name == null)&&(item.userInformations.length == 0))
					}),
					atts: attcs == null ? [] : attcs.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath,
						id: item.id
					})),
					audio: audio
				}
				this.setState({ loading: true });
				this.store.saveJournalByType({
					journal: params,
					saveType: 2
				}).then((res) => {
					if (res) {
						message.success("");
						this.setState({ loading: false });
						// 
						this.reset();

						const {pathname} = this.props;
						if (pathname == "/digital/draft") {  // 
							if (this.props.handleRoute) {
								this.props.handleRoute();
							}
						} else {
							// 
							if (this.props.getTable) {
								this.props.getTable();
							}
							if (this.props.onCancle) {
								this.props.onCancle();
							}
						}
					}
				});
			}
		});
	};

	onClose = () => {
		if (this.store.draftId != ""&&this.store.draftId != null) {  // 
			this.store.cancelJournal()
			.then((res)=>{
				if (res) {
					message.success("");
					this.props.form.resetFields();
					let detailData = JSON.parse(JSON.stringify(this.state.detailData));
					// 
					//this.child.reset();
					this.init(detailData);
					// 
					if (this.props.onCancle) {
						this.props.onCancle();
					}
				}
			});
		}
	}

	reset = () => {
		this.props.form.resetFields();
		this.store.draftId = ""; // id
		this.setState({
			assignments: [],
			tags: [],
			title: "",
			time: moment().format("YYYY-MM-DD"),
			attachments: [],
			people:"",
			target: "",
			content: "",
			conclusion: "",
			place:"",
			hours:"",
			audio: [],
			emails: [], // 
			userIds:[], // 
			isLoading: false,
			audioStatus: "",
			checks: [],
			ccEmails: [],
			ccUserIds: [],
		});
	}

	// 3pdf
	getReportUrl = (id) => {
		let _this = this;
		this.setState({
			isLoading: true
		});

		const temList = mobx.toJS(this.store.temList);
		const { title, time} = this.state;
		const numTime = moment(time).valueOf(); // 
		const newTime = moment(numTime).format("YYYYMMDD");
		const pdfName = newTime + `-${temList.name}-` + title;

		_this.timer22 = setInterval(() => {
			_this.store.getDownloadUrl(id).then((res) => {
				const {url} = res;
				if (url !== null && url !== "") {
					this.setState({
						isLoading: false
					});
					downloadFile(url, pdfName);  // pdf
					clearInterval(_this.timer22);
				}
				else {
					if (res.code==2) {
						this.setState({
							isLoading: false
						});
						clearInterval(_this.timer22);
					}
				}
			});
		}, 3000);
	}

	// word
	creatWord = (type) =>{
		const {assignments, tags, attachments, audio} = this.state;
		const temList = mobx.toJS(this.store.temList);
		let attcs = JSON.parse(JSON.stringify(attachments));

		// 
		if (Array.isArray(tags) && tags.length == 0) {
			message.warn("");
			return false;
		}

		// 
		if (!assignments.some((item) => {
			return (item.name == "" || item.name == null)&&(item.userInformations.length == 0)
		})) {
			if (assignments.some((item) => {
				return item.name == "" || item.name == null;
			})) {
				message.warn("");
				return false;
			}
			if (assignments.some((item) => {
				return item.userInformations.length == 0
			})) {
				message.warn("");
				return false;
			}
		}

		const { form } = this.props;
		// 
		form.validateFields((err, values) => {
			if (err) return;
			if (!err) {
				const {title, time, people, target, content, conclusion, place,hours} = values;
				const numTime = moment(time).valueOf(); // 
				const newTime = moment(numTime).format("YYYYMMDD");
				let journalSub = {
					title,
					time: numTime,
					people,
					place,
					hours,
					target: target,
					content: content,
					conclusion: conclusion
				};

				// 
				const param = {
					id: this.store.draftId,
					journalMain: {
						id: "",
						title: newTime + `-${temList.name}-` + title,
						userId: localStorage.getItem('userid'),
						userName: localStorage.getItem('username'),
						tags: tags.map(item => Object.assign({}, {id: "", name: item})),
						type: temList.type
					},
					journalSub: Base64.encode(JSON.stringify(journalSub)),
					assignments: assignments.filter((item)=>{
						return !((item.name == "" || item.name == null)&&(item.userInformations.length == 0))
					}),
					atts: attcs == null ? [] : attcs.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath,
						id: item.id
					})),
					audio
				}

				if (type==1) {
					this.store.wordDownload(param)
					.then((res) => {
						this.getReportUrl(res);
					});
				} else {
					this.store.pdfDownload(param)
					.then((res) => {
						this.getReportUrl(res);
					});
				}

			}
		});
	}

	// 
	sendReport = () => {
		let _this = this;
		const {emails, userIds, audio, checks, ccEmails, ccUserIds} = _this.state;
		const isemailUserIds = userIds == null ? [] : userIds;
		const isemails = emails == null ? [] : emails;
		// 
		if (isemailUserIds.length == 0 && isemails.length == 0) {
			message.warn("");
			return false;
		}

		const {assignments, tags, attachments} = this.state;
		const temList = mobx.toJS(this.store.temList);
		let attcs = JSON.parse(JSON.stringify(attachments));

		// 
		if (Array.isArray(tags) && tags.length == 0) {
			message.warn("");
			return false;
		}

		// 
		if (!assignments.some((item) => {
			return (item.name == "" || item.name == null)&&(item.userInformations.length == 0)
		})) {
			if (assignments.some((item) => {
				return item.name == "" || item.name == null;
			})) {
				message.warn("");
				return false;
			}
			if (assignments.some((item) => {
				return item.userInformations.length == 0
			})) {
				message.warn("");
				return false;
			}
		}

		const { form } = this.props;
		// 
		form.validateFields((err, values) => {
			if (err) return;
			if (!err) {
				const {title, time, people, place,hours} = values;
				const {target, content, conclusion} = this.state;
				const numTime = moment(time).valueOf(); // 
				const newTime = moment(numTime).format("YYYYMMDD");
				let journalSub = {
					title,
					time: numTime,
					people,
					place,
					hours,
					target: target.toHTML(),
					content: content.toHTML(),
					conclusion: conclusion.toHTML()
				};

				// 
				const param = {
					id: this.store.draftId,
					journalMain: {
						id: "",
						title: newTime + `-${temList.name}-` + title,
						userId: localStorage.getItem('userid'),
						userName: localStorage.getItem('username'),
						tags: tags.map(item => Object.assign({}, {id: "", name: item})),
						type: temList.type
					},
					journalSub: Base64.encode(JSON.stringify(journalSub)),
					assignments: assignments.filter((item)=>{
						return !((item.name == "" || item.name == null)&&(item.userInformations.length == 0))
					}),
					atts: attcs == null ? [] : attcs.map(item => Object.assign({
						fileId: item.fileId,
						fileName: item.fileName,
						filePath: item.filePath,
						id: item.id
					})),
					audio
				}

				const params = {
					journal: param,
					emails: emails,
					userIds: userIds == null ? [] : userIds.map(item => item.userId),
					attType: checks.map(item=>typeof item =="string"?Number(item):item),
					ccEmails,
					ccUserIds: ccUserIds == null ? [] : ccUserIds.map(item => item.userId)
				}

				this.store.sendEmail(params)
					.then((res) => {
						if (res == true) {
							message.success("");
						}
					});
			}
		});
	}

	init = (detailData) => {
		const journalSub = Base64.decode(detailData.journalSub == undefined ? "" : detailData.journalSub) || {};
		const sub = typeof journalSub == "string" ? JSON.parse(journalSub) : {};

		const assignments = detailData.assignments == null ? [] : detailData.assignments;
		const journalMain = detailData.journalMain || {};
		const atts = detailData.atts == null ? [] : detailData.atts;
		const audio = detailData.audio == null ? [] : detailData.audio;

		this.setState({
			detailData: detailData,
			title: sub.title,
			time: sub.time==null||sub.time==0?moment().format("YYYY-MM-DD"):moment(sub.time).format("YYYY-MM-DD"),
			people: sub.people,
			target: sub.target==null||sub.target==""?"":sub.target,
			content: sub.content==null||sub.content==""?"":sub.content,
			conclusion: sub.conclusion==null||sub.conclusion==""?"":sub.conclusion,
			place: sub.place,
			hours: sub.hours,
			assignments: assignments,
			tags: journalMain.tags==null?[]:journalMain.tags.map(item=>item.name),
			attachments: atts==null?[]:atts.map((vitem, vindex) => {
				return Object.assign({}, {
					uid: vitem.id==null||vitem.id==""?"!" + uuidv4():vitem.id,
					name: vitem.fileName,
					status: 'done',
					url: vitem.filePath,
					fileId: vitem.id==null?"!" + uuidv4():vitem.id,
					fileName:vitem.fileName,
					filePath: vitem.filePath,
					id: vitem.id==null?"!" + uuidv4():vitem.id,
				})
			}),
			emails: [], // 
			userIds:[], // 
			audio: audio,
			audioStatus: "",
			checks: [],
			ccEmails: [],
			ccUserIds: [],
		}, ()=>{
			// 
			this.props.form.setFieldsValue({
				target: sub.target==null||sub.target==""?"":sub.target,
				content: sub.content==null||sub.content==""?"":sub.content,
				conclusion: sub.conclusion==null||sub.conclusion==""?"":sub.conclusion,
			});
		});
	}

	updateAudioStaus = (type, data) => {
		this.setState({
			[type]: data
		});
	}

	onRefAudio = (ref) => {
		this.audio = ref
	}

	saveAttachment = (type, params) => {
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				type: 'IMAGE',
				url: item.filePath,
			})
		});
		let newArray = ContentUtils.insertMedias(this.state[type], arr);
		this.setState({
			[type]: newArray
		}, ()=> {
			this.props.form.setFieldsValue({
				[type]: newArray
			});
		});
	}

	onChangeAttc = (checkedValues) =>{
		this.setState({
			checks: checkedValues
		})
	}



	render() {
		const {assignments, tags, title, time, attachments, conclusion, content, target, hours, people, place, loading, emails, userIds, audio, checks,ccEmails, ccUserIds} = this.state;
		const {tagList, ossData} = this.store;
		const excludeControls = [
			'undo', 'redo', 'separator',
			'font-size', 'line-height', 'letter-spacing', 'separator',
			'text-color', 'underline', 'strike-through', 'bold', 'separator',
			'remove-styles', 'separator', 'text-indent', 'text-align', 'separator', 'hr', 'separator',
			'clear'
		];
		const disabledDate = (current) => {
			return current > moment();
		}
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 3},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 20},
			},
		};
		const formItemLayoutWithOutLabel = {
			wrapperCol: {
				xs: {span: 24, offset: 0},
				sm: {span: 20, offset: 3},
			}
		};
		let {detailData} = this.props;
		const journalMain = detailData.journalMain || {};
		const imageControls = [
			'align-left', // 
			'align-center', // 
			'align-right', // 
			'size',
			'remove'
		]
		const options = [
			{ label: 'Word', value: '0' },
			{ label: 'PDF', value: '1' }
		];
		return (
			<Form {...formItemLayout}>
				<Form.Item wrapperCol={{
								xs: {span: 24, },
								sm: {span: 24},
							}}
				           colon={false}
				           className="detail_form_bold">
					<div className="detail_form pr">
						<p className="form_title">
							{(title==""||title==null)||(time==""||time==null)?null: moment(time).format("YYYYMMDD") + "--" + title}
						</p>
						<p className="form_name">
							{journalMain.userName}
							<span
								className="form_time">{showText(moment(journalMain.createTime).format("YYYY-MM-DD HH:mm"), moment(journalMain.createTime).format("HH:mm"))}</span>
						</p>
						<AudioRecorder
							style={{
								position: 'absolute',
								top: 0,
								right: 0,
								width: '300px',
								height: '300px',
								zIndex: 3,
								textAlign: 'right'
							}}
							ossData={mobx.toJS(ossData)}
							audio={audio}
							updateState={this.updateState}
							updateAudioStaus={this.updateAudioStaus}
							onRef={this.onRefAudio}
							appendAliyundata={this.props.appendAliyundata}
							reSetAliyundata = {this.props.reSetAliyundata}
						/>
					</div>
				</Form.Item>
				<Form.Item {...formItemLayoutWithOutLabel}>
					<EditableTagGroup
						tagList={mobx.toJS(tagList)}
						tags={tags}
						updateState={this.updateState}
					/>
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('title', {
						initialValue: title || '',
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(<Input
						maxLength={15}
					          placeholder="15"
					          onChange={this.handleChange.bind(this, 'title')}/>)}
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('time', {
							initialValue: moment(time) || '',
							rules: [
								{
									required: true,
									message: '',
								},
							],
						}
					)(
						<DatePicker
							disabledDate={disabledDate}
							format="YYYY-MM-DD"
							onChange={this.onDateChange}/>
					)}
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('hours', {
						initialValue: hours || '',
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(<InputNumber
						onChange={this.handleNumChange.bind(this, 'hours')}
						placeholder=""
						style={{width: '177px'}}/>)}
					<span className="ml5"></span>
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('place', {
						initialValue: place || '',
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(<TextArea onChange={this.handleChange.bind(this, 'place')} autoSize={{minRows: 1}}
					             placeholder=""/>)}
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('people', {
						initialValue: people || '',
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(<TextArea onChange={this.handleChange.bind(this, 'people')} autoSize={{minRows: 1}}
					             placeholder=""/>)}
				</Form.Item>
				<Form.Item label={<span><span style={{
					fontWeight: "700",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span></span>} colon={false} className="edit_form_bold" style={{marginBottom: '3px'}}></Form.Item>
				<ReactKindeditor
					value={target}
					onChange={this.handleBChange.bind(this, 'target')}
					id="target"
				/>
				<Form.Item label={<span><span style={{
					fontWeight: "700",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span></span>} colon={false} className="edit_form_bold" style={{marginBottom: '3px'}}></Form.Item>
				<ReactKindeditor
					value={content}
					onChange={this.handleBChange.bind(this, 'content')}
					id="content"
				/>
				<Form.Item label={<span><span style={{
					fontWeight: "700",
					marginRight: "4px",
					color: "#f5222d",
					fontSize: "14px",
					fontFamily: "SimSun, sans-serif"
				}}>*</span></span>} colon={false} className="edit_form_bold" style={{marginBottom: '3px'}}></Form.Item>
				<ReactKindeditor
					value={conclusion}
					onChange={this.handleBChange.bind(this, 'conclusion')}
					id="conclusion"
				/>
				{/*<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('target', {
						initialValue: target,
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(
						<BraftEditor
							fontSizes={[
								12, 14, 16, 18, 20, 24,
								28, 30, 32, 36, 40, 48,
								56, 64
							]}
							onChange={this.handleBChange.bind(this, 'target')}
							className="my-editor"
							controls={excludeControls}
							contentStyle={{
								minHeight: 150,
								maxHeight: 400,
								overflowY: 'scroll',
								height: 'auto',
							}}
							extendControls={[
								{
									key: 'antd-uploader',
									type: 'component',
									component: (
										<MyUploadOne
											saveAttachment={this.saveAttachment.bind(this, "target")}
										/>
									)
								}
							]}
							imageControls={imageControls}
						/>
					)}
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('content', {
						initialValue: content,
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(
						<BraftEditor
							fontSizes={[
								12, 14, 16, 18, 20, 24,
								28, 30, 32, 36, 40, 48,
								56, 64
							]}
							value={content}
							onChange={this.handleBChange.bind(this, 'content')}
							className="my-editor"
							controls={excludeControls}
							contentStyle={{
								minHeight: 150,
								maxHeight: 400,
								overflowY: 'scroll',
								height: 'auto',
							}}
							extendControls={[
								{
									key: 'antd-uploader',
									type: 'component',
									component: (
										<MyUploadOne
											saveAttachment={this.saveAttachment.bind(this, "content")}
										/>
									)
								}
							]}
							imageControls={imageControls}
						/>
					)}
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					{getFieldDecorator('conclusion', {
						initialValue: conclusion,
						rules: [
							{
								required: true,
								message: '',
							},
						],
					})(
						<BraftEditor
							fontSizes={[
								12, 14, 16, 18, 20, 24,
								28, 30, 32, 36, 40, 48,
								56, 64
							]}
							onChange={this.handleBChange.bind(this, 'conclusion')}
							value={conclusion}
							className="my-editor"
							controls={excludeControls}
							contentStyle={{
								minHeight: 150,
								maxHeight: 400,
								overflowY: 'scroll',
								height: 'auto',
							}}
							extendControls={[
								{
									key: 'antd-uploader',
									type: 'component',
									component: (
										<MyUploadOne
											saveAttachment={this.saveAttachment.bind(this, "conclusion")}
										/>
									)
								}
							]}
							imageControls={imageControls}
						/>
					)}
				</Form.Item>*/}
				<Form.Item label="" colon={false} className="edit_form_bold"></Form.Item>
				<Row className="mb10">
					<Col span={12} className="tc">
						<div></div>
					</Col>
					<Col span={12} className="tc">
						<div></div>
					</Col>
				</Row>
				{assignments!==undefined&&assignments.map((vitem, vindex) => {
					return <Row key={`subname+${vindex}`}
					            style={{paddingTop: '10px', borderBottom: "1px dashed #E6E6E6"}}>
						<Col span={12} className="pr">
							<Form.Item
								label={" "}
								colon={false}
								labelCol={{
									xs: {span: 24},
									sm: {span: 6},
								}}
								wrapperCol={{
									xs: {span: 24},
									sm: {span: 16}
								}}
							>
								<Input value={vitem.name == null ? "" : vitem.name}
								       placeholder=""
								       onChange={this.onChange.bind(this, vindex)}/>
								<Popconfirm title="?" onConfirm={() => this.remove(vindex)}>
									<Button
										shape="circle"
										className="dynamic-delete-button"
										size="small"
										icon="minus"
										type="default"/>
								</Popconfirm>
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item
								label={" "}
								colon={false}
								labelCol={{
									xs: {span: 24},
									sm: {span: 6}
								}}
								wrapperCol={{
									xs: {span: 24},
									sm: {span: 16}
								}}
							>
								<PersonAddIcon
									data={vitem.userInformations == null ? [] : vitem.userInformations}
									updatePerson={this.updatePerson.bind(this, vindex)}
								/>
							</Form.Item>
						</Col>
					</Row>
				})}
				<Form.Item {...formItemLayoutWithOutLabel}>
					<Icon onClick={this.handleAdd}
					      style={{cursor: 'pointer', margin: '10px 0', color: '#4BA4BE', fontSize: '22px'}}
					      type="plus-circle"/>
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					<MyUpload
						saveAttachment={this.handleAttment.bind(this, 'attachments')}
						disabled={false}
						multiple={true}
						listType="picture-card"
						ossData={mobx.toJS(ossData)}
						fileList={attachments == null ? [] : attachments}
						journalType="journal"
					/>
				</Form.Item>
				<Form.Item label="" className="edit_form_bold">
					<Spin tip={this.state.tip} spinning={this.state.isLoading} delay={100}>
						<Button type="link" onClick={this.creatWord.bind(this, "1")}>Word</Button>
						<Button className="ml20" type="link" onClick={this.creatWord.bind(this, "2")}>PDF</Button>
					</Spin>
				</Form.Item>
				<Form.Item label="" className="edit_form_bold clearfix">
					<Button
						style={{position: 'relative', top: '4px'}}
						className="fl" type="link" onClick={this.sendReport}></Button>
					<div className="fl" style={{width:'275px'}}>
						<Checkbox.Group
							className="attc_checks"
							value={checks}
							options={options}
							onChange={this.onChangeAttc}
						/>
					</div>
				</Form.Item>
				<Form.Item {...formItemLayoutWithOutLabel}>
					<div className="fl mr25 ml15">
						<span></span>
						<EditableTagEmail2
							title=""
							data={emails}
							updateState={this.updateState.bind(this, 'emails')}
						/>
					</div>
					<div className="fl" style={{minWidth:'80px'}}>
						<PersonAdd2
							data={userIds}
							updatePerson={this.updateState.bind(this, 'userIds')}
							disabled={false}
							title=""
						/>
					</div>
				</Form.Item>
				<Form.Item {...formItemLayoutWithOutLabel}>
					<div className="fl mr25 ml15">
						<span></span>
						<EditableTagEmail2
							title=""
							data={ccEmails}
							updateState={this.updateState.bind(this, 'ccEmails')}
						/>
					</div>
					<div className="fl" style={{minWidth:'80px'}}>
						<PersonAdd2
							data={ccUserIds}
							updatePerson={this.updateState.bind(this, 'ccUserIds')}
							disabled={false}
							title=""
						/>
					</div>
				</Form.Item>
				<Form.Item
					wrapperCol={{
						xs: {span: 24},
						sm: {span: 24}
					}}
                    className="clearfix tr mt20 pl20 pr20 fixed_bottom"
					style={{
						width: this.props.appStore.collapsed?"calc(100% - 470px)":" calc(100% - 590px)"
					}}
				>
					<Button className="fl"
					        shape="round"
					        style={{
					        	position: 'relative',
						        top: '4px'
					        }}
					        type="primary"
					        onClick={this.saveDraft}>
						
					</Button>
					<Popconfirm placement="top" title="?" onConfirm={() => this.onClose()}>
						<Button shape="round" style={{marginRight: 8}}>
							
						</Button>
					</Popconfirm>
					<Button shape="round"
					        loading={loading}
					        onClick={this.handleSubmit}
					        type="primary">
						
					</Button>
				</Form.Item>
			</Form>
		);
	}
}

const editForm = Form.create({})(EditeMeeting);
export default editForm;



