import React, {Component} from "react";
import { Icon, Tooltip, message, Button, Popover, Spin } from 'antd';
import {inject, observer} from 'mobx-react';
import * as  mobx from 'mobx';
import CommonEmail2 from "@/components/digitalOrg/CommonEmail2";
import PersonEdit from "@/components/digitalOrg/PersonEdit";

@observer
export default class CommentList extends Component {
	constructor(props) {
		super(props)
		this.store = this.props.store;
		this.state = {
			visible: false,
			title: "",
			editvisible: false,
			data: [],
			loading: false
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {

	}

	// 
	handleZan = () =>{
		this.store.addOrDelLike()
		.then((res)=>{
			if (res==true) {
				message.success("");
				// 
				this.store.getLikeUsersByJournalId();
			}
		});
	}

	// 
	handCollection = () =>{
		this.store.addOrDelCollection()
		.then((res)=>{
			if (res==true) {
				message.success("");
				// 
				this.store.getJournalIndicators();
			}
		});
	}

	handleVisibleChange = visible => {
		this.setState({ visible });
	};

	// 
	scrollToAnchor(key) {
		const {scrollToAnchor} = this.props;
		scrollToAnchor(key);
	}

	// 
	handleEdit = () => {
		this.store.getSharedEditingList().then((res)=>{
			if (res==null||(Array.isArray(res)&&res.length==0)) {
				this.setState({
                   title: "",
				   editvisible: true,
				   data: res==null?[]: res
				});
			} else {
				this.setState({
					title: "",
					editvisible: true,
					data: res==null?[]: res
				});
			}
		});
	}

	onClose = (type, param) =>{
		this.setState({
			[type]: param
		});
	}

	updateState = (type, param) => {
		this.setState({
			[type]: param
		});
	}

	render() {
		const {getLikeUsers, getComments, journalIndicators, } = this.store;
		const username = localStorage.getItem('username');
		const isLike = mobx.toJS(getLikeUsers).filter(item=>item.userName==username).length>0?true:false;
		const {haveCollection} = mobx.toJS(journalIndicators);
		const {pathname, detailData, isWeekShow, userId} = this.props;
		const isShow = pathname!==undefined&&pathname=="/digital/office"?true:false;
		const {editvisible, title, data, loading} = this.state;
		// 
		let isWeek = isWeekShow==undefined?false:isWeekShow!=undefined&&(isWeekShow);

		return (<span>
			   <Tooltip title="">
                  <Icon type="message" className="commentlist_icon" onClick={this.scrollToAnchor.bind(this, "commentlist")}/>
                </Tooltip> <span className="commentlist_title">{mobx.toJS(getComments).length}</span>
				<Tooltip title="">
					{isLike?<Icon type="like" theme="filled" className="commentlist_icon pointer" onClick={this.handleZan}/>: <Icon type="like" className="commentlist_icon pointer" onClick={this.handleZan}/>}
	                </Tooltip><span className="commentlist_title">{mobx.toJS(getLikeUsers).length}</span>
				<Tooltip title="">
	                 <Icon type="eye" className="commentlist_icon"/>
	            </Tooltip><span className="commentlist_title">{mobx.toJS(journalIndicators).viewCount||0}</span>
				{!isShow?<Tooltip title={""}>
					<Icon theme={haveCollection==1?"filled":""} type="star" className="commentlist_icon pointer" onClick={this.handCollection}/>
				</Tooltip>:null}
				<Popover
					overlayClassName="digital_popver"
					placement="bottom"
					content={<CommonEmail2
						updateState={this.updateState}
						detailData={detailData}
						store={this.store} />}
					trigger="click"
					visible={this.state.visible}
					onVisibleChange={this.handleVisibleChange}
				>
	               <Button className="ml10" type="link"></Button>
	            </Popover>
			   {isWeek?<Button type="link" onClick={this.handleEdit}></Button>:null}
			     <PersonEdit
			       title={title}
			       visible={editvisible}
			       onClose={this.onClose.bind(this, "editvisible", false)}
			       data={data}
			       store={this.store}
			     />
              <Spin wrapperClassName="dib" tip=",..." spinning={loading} />
			</span>);
	}
}



