/*
* 
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Icon, Table, Pagination, ConfigProvider, Popconfirm, Tooltip, Progress, message, Checkbox, Button, Form, Input, InputNumber, DatePicker} from 'antd';
import * as mobx from 'mobx';
import {withRouter} from 'react-router-dom';
import {Scrollbars} from 'react-custom-scrollbars';
import DetailModal from '@/components/digitalOrg/DetailModal';
import DetailOfficeModal from '@/components/digitalOrg/DetailOfficeModal';
import moment from 'moment';
let officeArray = [""];  //  

const {TextArea} = Input;
const customizeRenderEmpty = () => (
	<div style={{textAlign: 'center', padding: '0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png'/>
		<p style={{
			fontSize: '14px',
			fontFamily: 'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}></p>
	</div>
);
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
	state = {
		editing: false,
	};

	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};

	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			delete values["completeTime"];
			this.toggleEdit();
			handleSave({...record, ...values});
		});
	};

	//
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('completeTime')) {
				values["completeTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			this.toggleEdit();
			handleSave({...record, ...values});
		});
	}

	showStatus = (value) => {
		if (value == 3) {
			return "#F5222D";
		} else if (value == 1) {
			return "#1890FF";
		} else if (value == 2) {
			return "#52C41A";
		} else {
			return "#333";
		}
	}

	onBlur = () => {
		this.toggleEdit();
	}

	//
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.beginTime) {
			return false
		}
		return endValue.valueOf() < moment(moment(record.beginTime).format('YYYY-MM-DD')).valueOf()
	}

	// 
	numberChange = (value) => {
		if (value==null) return false;
		if (value>=100) {
			message.warn("100, !");
			return false;
		}
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title} = this.props;
		const {editing} = this.state;
		const username = localStorage.getItem("username");

		// 
		let isEdit = record.userInformations.filter(item=>item.userName==username).length>0;

		if (dataIndex == "remark") {  // 
			return editing&&isEdit ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<TextArea
						autoFocus={true}
						ref={node => (this.input = node)}
						onPressEnter={this.save}
						onBlur={this.save}
						autoSize={{minRows: 1, maxRows: 3}}
					/>)}
				</Form.Item>
			) : (
				<div
					className={isEdit?"editable-cell-value-wrap":""}
					style={{
						paddingRight: 24,
						minHeight: '34px'}}
					onClick={isEdit?this.toggleEdit:null}
				>
					{children}
				</div>
			);
		} else if (dataIndex == "completeTime") {  // 
			// 
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing&&isEdit ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						autoFocus={true}
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						mode="date"
						//
						disabledDate={this.disabledEndDate}
						style={{width:'118px'}}
						onOpenChange={(status) => {
							if(status){
								this.setState({editing: true})
							} else {
								this.setState({editing: false})
							}
						}}
						onChange={this.saveDate}
						open={editing}
					/>
				)}
			</Form.Item>) : (<div
					className={isEdit?"editable-cell-value-wrap":""}
					style={{minHeight: '31px'}}
					onClick={isEdit?this.toggleEdit:null}
				>
					{children}
				</div>
			);
		} else {   // 
			return editing&&isEdit ? (
				<Tooltip
					trigger={['focus']}
					title={"0-100"}
					placement="top"
					overlayClassName="numeric-input"
				>
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [
							{
								required: false,
								message: `${title} is required.`,
							},
						],
						initialValue: record[dataIndex],
					})(<InputNumber
							autoFocus={true}
							defaultValue={record.progress == "0" ? 0 : Number(record.progress)}
							min={0}
							max={100}
							precision={0}
							ref={node => (this.input = node)}
							onChange={this.numberChange}
							onBlur={this.save}
						/>
						)}
				</Form.Item>
				</Tooltip>
			) : (<div>{Number(record.progress) >= 100 ? <span style={{
						color: this.showStatus(record.state)
					}}></span>:<span
							className={isEdit?"editable-cell-value-wrap":""}
							style={{
								width: "80px",
								minHeight: '34px',
								color: this.showStatus(record.state)
							}}
							onClick={isEdit?this.toggleEdit:null}
						>
							{Number(record.progress) + "%"}
					</span>}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}


@withRouter
@inject('OfficeManageStore')
@observer
export default class DigtalList extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.officeStore = this.props.OfficeManageStore;
		this.state = {
			tableHeight: 250,
			visible: false,
			visibleOffic: false,
			relationType: [],
			userId: [],
			sortedInfo: null,
			state: [],
		}
	}

	componentWillReceiveProps(nextProps) {

	}

	componentDidMount() {
		window.addEventListener('resize', this.changeHeight)
		this.changeHeight();
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.changeHeight)
	}

	changeHeight = () => {
		let tabpaneDivClientHeight = document.getElementById('digital_card').clientHeight;
		this.setState({tableHeight: tabpaneDivClientHeight - 100});
	}

	onShowSizeChange = (current, pageSize) => {
		this.store.queryParms = Object.assign(mobx.toJS(this.store.queryParms), {
			pageIndex: current,
			pageSize: pageSize
		});
		this.store.getAssignmentPageList();
	}

	onChange = (pageNumber) => {
		this.store.queryParms = Object.assign(mobx.toJS(this.store.queryParms), {
			pageIndex: pageNumber
		});
		this.store.getAssignmentPageList();
	}

	// 
	handleDelete = (id) => {
		this.store.delAssignment(id)
		.then((res) => {
			if (res) {
				message.success("");
				this.store.getAssignmentPageList();
				// 
				this.store.getMyInfo();
			}
		});
	}

	showDrawer = (record) => {
		if (officeArray.includes(record.relationType)) {  //
			this.officeStore.getJournalTypeList().then((res)=>{
				this.officeStore.temList = Object.assign({}, mobx.toJS(this.officeStore.digtalTemData).filter(item=>item.name==record.relationType)[0]);
			});
			this.officeStore.selestedId=record.relationId;
			this.officeStore.getJournalById(record.relationId)
			.then((res) => {
				if (res) {
					this.setState({
						visibleOffic: true,
					}, () => {
						this.officeStore.detailData = Object.assign({}, res);
					});
				}
			})
			// 
			this.officeStore.getLikeUsersByJournalId();
			// 
			this.officeStore.getCommentsByJournalId();
			this.officeStore.getJournalIndicators();
			this.officeStore.isEdit =false;
		} else {
			this.store.getJournalTypeList().then((res)=>{
				this.store.temList = Object.assign({}, mobx.toJS(this.store.digtalTemData).filter(item=>item.name==record.relationType)[0]);
			});
			this.store.selestedId=record.relationId;
			this.store.getJournalById(record.relationId)
			.then((res) => {
				if (res) {
					this.setState({
						visible: true
					}, () => {
						this.store.detailData = Object.assign({}, res);
					});
				}
			})
			// 
			this.store.getLikeUsersByJournalId();
			// 
			this.store.getCommentsByJournalId();
			this.store.getJournalIndicators();
			this.store.isEdit =false;
		}
	};

	onClose = (type) => {
		this.setState({
			[type]: false
		});
	};

	// 
	onChangeState = (type, item, e) => {
		let newData = JSON.parse(JSON.stringify(this.state[type]));
		let index = newData.findIndex(vitem => vitem == item.id);
		const {checked} = e.target;
		if (index == -1) {  // 
			this.setState({
				[type]: [...newData, item.id]
			});
		} else {
			if (checked == false) { // 
				newData.splice(index, 1);
				this.setState({
					[type]: Object.assign([], newData)
				});
			}
		}
	}

	// 
	handleSearch = (confirm, dataIndex) => {
		confirm();
		this.store.queryParms[dataIndex] = Object.assign([], this.state[dataIndex]);
		this.store.queryParms.pageIndex = 1;
		this.store.queryParms.pageSize = 10;
		this.store.getAssignmentPageList();
	};

	// 
	handleReset = (clearFilters, dataIndex) => {
		clearFilters();
		this.store.queryParms[dataIndex] = Object.assign([]);
		this.store.queryParms.pageIndex = 1;
		this.store.queryParms.pageSize = 10;
		this.store.getAssignmentPageList();
		this.setState({
			[dataIndex]: Object.assign([])
		});
	};

	getColumnSearchProps = dataIndex => ({
		filterDropdown: ({confirm, clearFilters}) => (
			<div style={{padding: 8}}>
				<Scrollbars
					autoHide
					autoHideTimeout={1000}
					autoHideDuration={200}
					autoHeight
					autoHeightMin={'88px'}
					autoHeightMax={'300px'}
					thumbMinSize={30}
					universal={true}>
					{mobx.toJS(this.store[dataIndex]).map((item, index) => {
						return (<div className="pl10"><Checkbox
							className="db"
							checked={this.state[dataIndex].includes(item.id)}
							key={dataIndex + "-" + index}
							onChange={this.onChangeState.bind(this, dataIndex, item)}>{item.typeName}</Checkbox></div>)
					})}
				</Scrollbars>
				<Button
					type="primary"
					onClick={() => this.handleSearch(confirm, dataIndex)}
					size="small"
					style={{width: 60, marginRight: 8, marginTop: 8}}
				>
					
				</Button>
				<Button onClick={() => this.handleReset(clearFilters, dataIndex)} size="small"
				        style={{width: 60, marginTop: 8}}>
					
				</Button>
			</div>
		),
		filterIcon: filtered => (
			<Icon className="bocfiterIcon" type="filter" style={{color: filtered ? '#1890ff' : undefined}} />
		),
		onFilter: (value, record) => {
		},
		onFilterDropdownVisibleChange: visible => {
			if (visible) {
			}
		},
		render: (record, text) => {
			if (dataIndex == "userName") {
				return text["userName"];
			} else if (dataIndex == "relationType") {
				return <a
					style={{
						cursor: 'pointer'
					}}
					onClick={(e) => {
					this.showDrawer(text);
				}}>
					{text["relationType"]}
				</a>;
			} else if (dataIndex == "userId") {
				const val = mobx.toJS(text)["userInformations"];
				const title = typeof (val) !== 'undefined' ? val.map((item) => {
					return item.userName}).join('') : '';
				return typeof (val) !== 'undefined' ? <Tooltip placement="topLeft" title={title}>
					<span>{title}</span>
				</Tooltip>: ''
			}
		}
	});

	// 
	handleChangeOrder = (pagination, filters, sorter,) => {
		const sortVlaue = sorter.field;
		this.setState({
			sortedInfo: sorter,
		});
		if (sorter.order == "ascend") {
			this.store.queryParms = Object.assign(mobx.toJS(this.store.queryParms), {
				ordervalue: sortVlaue == "beginTime" ? 1 : 2,
				orderType: 1
			});
			this.store.getAssignmentPageList();
		} else if (sorter.order == "descend") {
			this.store.queryParms = Object.assign(mobx.toJS(this.store.queryParms), {
				ordervalue: sortVlaue == "beginTime" ? 1 : 2,
				orderType: 0
			});
			this.store.getAssignmentPageList();
		}
	};

	handleSave = row => {
		this.store.saveAssignment(row).then((res) => {
			if (res) {
				message.success("");
				this.store.getAssignmentPageList();
				if (res.state==2) {  // 
					// 
					this.store.getMyInfo();
				}
			}
		});
	};

	showTextStatus = (value) => {
		if (value == 3) {
			return "#F5222D";
		} else if (value == 1) {
			return "#1890FF";
		} else if (value == 2) {
			return "#52C41A";
		} else {
			return "#333";
		}
	}

	render() {
		let {tableHeight, visible, sortedInfo, visibleOffic} = this.state;
		const {tableData, totalCount, listLoading, queryParms, hasJournal} = this.store;
		const current = mobx.toJS(queryParms).pageIndex;
		sortedInfo = sortedInfo || {};

		let columns =
			!hasJournal ?
				[
					{
						title: 'ID',
						dataIndex: 'serial',
						align: 'left',
						width: '150px',
						ellipsis: true,
						render: (text, record) => {
							return <span title={text}>{text}</span>;
						},
					},
					{
						title: '',
						dataIndex: 'name',
						align: 'center',
						render: (text, record) => {
							return (<div style={{
								wordWrap:'break-word',
								whiteSpace: 'pre-wrap',
								fontWeight: 600,
								color: this.showTextStatus(record.state)}}>{text}</div>)
						}
					},
					{
						title: '',
						dataIndex: 'userInformations',
						align: 'center',
						ellipsis: true,
						...this.getColumnSearchProps('userId')
					},
					{
						title: '',
						dataIndex: 'relationType',
						align: 'center',
						...this.getColumnSearchProps('relationType')
					},
					{
						title: '', dataIndex: 'progress', align: 'center',editable: true,
						...this.getColumnSearchProps('state')
					},
					{
						title: '',
						dataIndex: 'beginTime',
						align: 'center',
						ellipsis: true,
						render: (text, record) => {
							return text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD");
						},
						sorter: (a, b) => a.beginTime - b.beginTime,
						sortOrder: sortedInfo.columnKey === 'beginTime' && sortedInfo.order,
					},
					{
						title: '',
						dataIndex: 'completeTime',
						align: 'center',
						ellipsis: true,
						editable: true,
						render: (text, record) => {
							return text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD");
						},
						sorter: (a, b) => a.completeTime - b.completeTime,
						sortOrder: sortedInfo.columnKey === 'completeTime' && sortedInfo.order,
					},
					{title: '', dataIndex: 'remark', align: 'center', ellipsis: true, editable: true,
						render: (text, record) => {
							return (<Tooltip placement="topLeft" title={text}>
								<div className="ellsis">{text}</div>
								</Tooltip>)
						}
					}
				] : [
					{
						title: 'ID',
						dataIndex: 'serial',
						align: 'left',
						width: '150px',
						ellipsis: true,
						render: (text, record) => {
							return <span title={text}>{text}</span>;
						},
					},
					{
						title: '',
						dataIndex: 'name',
						align: 'center',
						render: (text, record) => {
							return (<div style={{
								wordWrap:'break-word',
								whiteSpace: 'pre-wrap',
								fontWeight: 600,
								color: this.showTextStatus(record.state)}}>{text}</div>)
						}
					},
					{
						title: '',
						dataIndex: 'userInformations',
						align: 'center',
						ellipsis: true, ...this.getColumnSearchProps('userId')
					},
					{
						title: '',
						dataIndex: 'relationType',
						align: 'center',
						...this.getColumnSearchProps('relationType')
					},
					{
						title: '',
						dataIndex: 'progress',
						align: 'center',
						editable: true,
						...this.getColumnSearchProps('state')
					},
					{
						title: '', dataIndex: 'beginTime', align: 'center', ellipsis: true, render: (text, record) => {
							return text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD");
						},
						sorter: (a, b) => a.beginTime - b.beginTime,
						sortOrder: sortedInfo.columnKey === 'beginTime' && sortedInfo.order,
					},
					{
						title: '',
						dataIndex: 'completeTime',
						align: 'center',
						ellipsis: true,
						render: (text, record) => {
							return text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD");
						},
						sorter: (a, b) => a.completeTime - b.completeTime,
						sortOrder: sortedInfo.columnKey === 'completeTime' && sortedInfo.order,
						editable: true,
					},
					{title: '', dataIndex: 'remark', align: 'center', ellipsis: true, editable: true,
						render: (text, record) => {
							return (<Tooltip placement="topLeft" title={text}>
								<div className="ellsis">{text}</div>
							</Tooltip>)
						}
					},
					{
						title: '',
						key: 'action',
						align: 'center',
						width: '80px',
						render: (text, record) => (
							<Popconfirm
								placement="leftTop"
								title="?" onConfirm={() => this.handleDelete(record.id)}>
								<a style={{fontSize: '12px'}}></a>
							</Popconfirm>
						)
					}
				]

		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const cols = columns.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					handleSave: this.handleSave,
				}),
			};
		});

		return (
			<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<Table
					rowKey={record => record.id}
					className="digital_table"
					columns={cols}
					dataSource={mobx.toJS(tableData)}
					pagination={false}
					loading={listLoading}
					onChange={this.handleChangeOrder}
					components={components}
					rowClassName={() => 'editable-row'}
				/>
				<Pagination
					className="mt20 tc"
					onShowSizeChange={this.onShowSizeChange}
					onChange={this.onChange}
					total={totalCount}
					showQuickJumper
					showTotal={total => ` ${total} `}
					current={current}
				/>
				<DetailModal
					visible={visible}
					onClose={this.onClose.bind(this, "visible")}
					store={this.store}
				/>
				<DetailOfficeModal
					visible={visibleOffic}
					onClose={this.onClose.bind(this, "visibleOffic")}
					store={this.officeStore}
				/>
			</ConfigProvider>
		)
	}
}




