import React from 'react';
import {Drawer, message} from 'antd';
import {inject, observer} from 'mobx-react'
import * as mobx from 'mobx';
import {withRouter} from 'react-router-dom';

import DetailForm from './DetailForm';
import DetailFormMonthlySharing from './DetailFormMonthlySharing';
import DetailFormVisit from './DetailFormVisit';
import DetailFormSummary from './DetailFormSummary';
import DetailFormReplay from './DetailFormReplay';
import DetailFormNews from './DetailFormNews';
import DetailFormWeekReport from './DetailFormWeekReport';
import DigitalComment from "@/components/digitalOrg/DigitalComment";

import 'braft-editor/dist/index.css';

@withRouter
@observer
class DetailModal extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {

		}
	}

	componentDidMount() {

	}

	onAsync = (type, id, index) => {
		this.store.syncAssignment(id).then((res) => {
			if (res) {
				message.success("");
				// 
				let detailData = JSON.parse(JSON.stringify(this.store.detailData));
				let assignments = detailData[type];
				assignments[index].isSync = 1; // 
				this.store.detailData = Object.assign(detailData, {
					[type]: assignments
				});
			}
		})
	}

	// 
	handleAddNew = (detail) =>{
		this.store.saveAssignments(detail).then((res) => {
			if (res) {
				message.success("");
				// 
				this.store.getJournalById(this.store.selestedId)
				.then((res) => {
					this.store.detailData = Object.assign({}, res);
					this.child.handleCalc();
				});

			}
		});
	}

	onRef = (ref) => {
		this.child = ref
	}

	reset = () => {
		const {onClose} = this.props;
		onClose();
	}

	render() {
		const {visible} = this.props;
		const {detailData, hasJournal, temList} = this.store;
		const temType = mobx.toJS(temList).type;
		return (
			<Drawer
				width={"70%"}
				title=""
				placement="right"
				closable={true}
				onClose={this.reset}
				visible={visible}
			>
				{temType==="meeting"?<DetailForm
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				{temType==="monthlySharing"?<DetailFormMonthlySharing
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				{temType==="visit"?<DetailFormVisit
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				{temType==="summary"?<DetailFormSummary
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				{temType==="replay"?<DetailFormReplay
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				{temType==="news"?<DetailFormNews
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				{temType==="weekreport"?<DetailFormWeekReport
					store={this.store}
					detailData={mobx.toJS(detailData)}
					hasJournal={hasJournal}
					onAsync={this.onAsync}
					handleAddNew={this.handleAddNew}
					onRef={this.onRef}
					visible={visible}
				/>:null}
				<DigitalComment
					store={this.store}
				/>
			</Drawer>
		);
	}
}

export default DetailModal;



