import React from 'react';
import {inject, observer} from 'mobx-react';
import {Row, Col, Avatar, Card, Input} from 'antd';
import DigtalList from '@/components/digitalOrg/DigtalList';
import moment from 'moment';
import * as  mobx from 'mobx';

const {Search} = Input;
const chinese = {1: "", 2: "", 3: "", 4: "", 5: "", 6: "", 0: ""};

function transHours(hour, name) {
  if (hour>=0&&hour<12) {
     return (<span>{name}</span>);
  } else if (hour>=12&&hour<18) {
	  return (<span>{name}</span>);
  } else {
	  return (<span>{name}</span>);
  }
}

@inject('DigtalOrgStore')
@observer
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.DigtalOrgStore;
		this.state = {
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {

	}

	componentDidMount() {
		this.store.getMyInfo();
		// 
		this.store.queryParms = Object.assign(mobx.toJS(this.store.queryParms), {
			text: "",
			userId: [],
			relationType: [],
			state: [],
			ordervalue: 0,
			orderType: 0,
			pageIndex: 1,
			pageSize: 10
		});
		this.store.getAssignmentPageList();
		this.store.getUserPermissionByAppCode();
		this.store.getAssignmentUser();
		this.store.getJournalTypeList();
		this.store.getJournalTagList();
	}

	componentWillMount() {

	}

	// 
	onSearch = (value) => {
		this.store.queryParms = Object.assign(mobx.toJS(this.store.queryParms), {
			text: value
		});
		this.store.getAssignmentPageList();
	}

	handleRoute = () => {
		const {protocol, host} = window.location;
		let origin = protocol+"//"+host+"/"+"ci"+"/#"+"/honour";
		window.open(origin, "_blank");
		//this.props.history.push("/honour")
	}

	render() {
		const date = moment();
		const hour = moment().hours();
		const {myInfo} = this.store;
		const myINFO = mobx.toJS(myInfo);
		const pageHeaderContent = (
			<div className={"pageHeaderContent"}>
				<div className={"avatar"}>
					<Avatar
						size="large"
						src={'./images/logo_new.png'}
					/>
				</div>
				<div className={"content"}>
					<div className={"contentTitle"}>{transHours(hour, myINFO.name)}</div>
					<div className={"contentTitle_next"}>{myINFO.station} | {myINFO.department}</div>
				</div>
			</div>
		);
		const extraContent = (<div className="extraContent">
				<div className="statItem">
					<p></p>
					<p>{myINFO.todoCount}</p>
				</div>
				<div className="statItem">
					<p></p>
					<p>{myINFO.overCount}</p>
				</div>
				<div className="statItem">
					<p></p>
					<p>{myINFO.journalCount}</p>
				</div>
			</div>
		);

		return (
			<div className="w">
				<Card className="digital-card" style={{width: '100%'}}>
					<h1 className="digital-title">{moment(date).format('YYYYMMDD')} {"" + chinese[moment(date).day()]} {moment(date).format('HH:mm')}</h1>
					<Row>
						<Col span={12}>
							{pageHeaderContent}
						</Col>
						<Col span={12}>
							{extraContent}
						</Col>
					</Row>
				</Card>
				<div className="digital-banner mt10 pr">
					<img
						onClick={this.handleRoute}
						style={{cursor: "pointer", width: "100%", height: "108px"}}
						src="https://bocspace.oss-cn-beijing.aliyuncs.com/ci/digital_banner.gif" alt=""/>
						{/*<span onClick={this.handleRoute} style={{background: "transparent", position: "absolute", right: "26%", bottom: "21px", cursor: "pointer", width: "166px", height: "33px", display: "inline-block"}}></span>*/}
				</div>
				<div id="digital_card"
				     style={{width: '100%'}}>
					<Card className="digital-card mt10"
					      title=""
					      extra={<Search
						      placeholder=""
						      onSearch={this.onSearch}
						      style={{width: 252}}
					      />}
					      style={{width: '100%'}}>
						<DigtalList
							store={this.store}
						/>
					</Card>
				</div>
			</div>
		);
	}
}





