import React from 'react';
import {inject, observer} from 'mobx-react';
import {Icon, Input, Col, Row, Modal, Avatar, message, Button, Empty} from 'antd';
import {Scrollbars} from 'react-custom-scrollbars';
import * as _ from 'lodash'

const colorList = ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae', '#F08080', '#EEE685', '#9400D3', '#D1EEEE', '#90EE90']; // 

@inject('UserStore')
@observer
class PersonEdit extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			text: '',
			visible: false,
			oldSelect: this.props.data, // ,
			defaultData: [], // 
			data: [], // 
			filterData: [], // 
		}
		this.getData = this.getData.bind(this);
		this.handleClickDebounce = _.debounce(this.getData, 500, {'maxWait': 1000});
	}

	componentWillReceiveProps(nextProps, nextContext) {
		this.setState({
			oldSelect: nextProps.data
		}, () => {
			this.init();
		});
	}

	componentDidMount() {
		const params = "";
		// 
		this.props.UserStore.getRole(params).then((res) => {
			this.setState({
				defaultData: res.body.map((item) => Object.assign(item, {selected: false})),
			}, () => {
				this.init();
			});
		});
	}

	// 
	init = () => {
		let defaultDatas = JSON.parse(JSON.stringify(this.state.defaultData));
		//  
		let arr = defaultDatas.map((item) => {
			let obj = {}
			if ([...this.state.oldSelect].some(v => v === item.id)) {
				obj = Object.assign(item, {selected: true})
			} else {
				obj = Object.assign(item, {selected: false})
			}
			return obj;
		});
		this.setState({
			data: Object.assign([], arr)
		});
	}

	componentWillUnmount() {
		this.handleClickDebounce.cancel();
	}

	// 
	handleOk = () => {
		let dataArray = JSON.parse(JSON.stringify(this.state.data));
		const users = dataArray.filter((res) => {
			return res.selected == true
		}).map((item, index) => {
			return item.id;
		});

		// 
		this.store.saveSharedEditing(users).then((res)=>{
			if (res) {
				message.success("");
				this.handleCancel();
			}
		});
	};

	// 
	handleCancel = () => {
		this.setState({
			visible: false,
			text: "",
			data: Object.assign([], this.state.defaultData),
			oldSelect: this.props.data
		});
		this.props.onClose();
	};

	// 
	handClick = (param) => {
		let dataArray = JSON.parse(JSON.stringify(this.state.data));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selected: true}))
		this.setState({
			data: Object.assign([], dataArray)
		})
	}

	// 
	handClickDel = (param) => {
		let dataArray = JSON.parse(JSON.stringify(this.state.data));
		const dindex = dataArray.findIndex((item) => {
			return item.id == param.id
		})
		dataArray.splice(dindex, 1, Object.assign(param, {selected: false}))
		this.setState({
			data: Object.assign([], dataArray)
		})
	}

	// 
	onChange = (e) => {
		this.setState({
			text: _.trim(e.target.value)
		});
		this.handleClickDebounce()
	}

	getData() {
		const {text, data} = this.state;
		let dataArray = JSON.parse(JSON.stringify(data));
		let filterData = dataArray.filter(item => (item.realname).indexOf(text) > -1 || (item.phone).indexOf(text) > -1)
		this.setState({
			filterData: filterData
		});
	}

	render() {
		const {data, oldSelect, filterData, text, defaultData} = this.state;
		const {disabled, title, visible} = this.props;
		let isFilter = text == "" || text == null;
		const activeData = isFilter ? data : filterData;  // 
		return (<Modal
				zIndex="1099"
				maskClosable={false}
				closable={false}
				okText={''}
				title={title||""}
				visible={visible}
				onOk={this.handleOk}
				onCancel={this.handleCancel}
			>
				<Row type="flex" justify="space-between" className="">
					<Col span={12} className='pr20'>
						<Input
							value={text}
							placeholder="/"
							suffix={<Icon type="search"/>}
							onChange={this.onChange}
						/>
						<Scrollbars
							autoHide
							autoHideTimeout={1000}
							autoHideDuration={200}
							autoHeight
							autoHeightMin={0}
							autoHeightMax={350}
							thumbMinSize={30}
							universal={true}
						>
							<ul className="userlist_ul">
								{activeData.length > 0 && activeData.map((item, index) => {
									return <li
										key={index}
										className="pr"
										onClick={this.handClick.bind(this, item)}>
										<div className="ul_name">{item.realname}</div>
										<div className="ul_station">{item.station}</div>
										<div className="ul_email">{item.email}</div>
										<Icon
											style={{
												position: 'absolute',
												top: '10px',
												right: '15px',
												fontSize: '16px',
												color: 'rgba(75,164,190,1)'
											}}
											className={item.selected ? "active" : "hidden"} type="check"/>
									</li>
								})
								}
							</ul>
						</Scrollbars>
						{activeData.length == 0 ?
							<Empty
								image="https://gw.alipayobjects.com/mdn/miniapp_social/afts/img/A*pevERLJC9v0AAAAAAAAAAABjAQAAAQ/original"
								imageStyle={{
									height: 60,
									marginTop: "30px"
								}}
								description={
									""
								}
							>
							</Empty>
							: null}
					</Col>
					<Col span={12} className="tl" style={{
						minHeight: '350px',
						borderLeft: '1px solid #e6e6e6'
					}}>
						<p className="pl20">({data && data.filter((res) => {
							return res.selected == true
						}).length})</p>
						<ul className="userlist_ul_del">
							{data.filter((res) => {
								return res.selected == true
							}).length > 0 && data.filter((res) => {
								return res.selected == true
							}).map((item, index) => {
								return <li
									key={index}
									className="pr"
									onClick={this.handClickDel.bind(this, item)}>
									<Avatar
										style={{
											display: 'inlineBlock',
											backgroundColor: colorList[index % 10],
											verticalAlign: 'middle'
										}} size="small">
										{item.realname.slice(0, 1)}
									</Avatar> {/**/}
									<span className="ul_name">{item.realname}</span>
									<Icon
										style={{
											position: 'absolute',
											top: '13px',
											right: '15px',
											fontSize: '16px',
											color: 'rgba(184,80,74,1)',
											cursor: 'pointer'
										}}
										className="userlist_ul_icon"
										type="close"/>
								</li>
							})
							}
						</ul>
					</Col>
				</Row>
			</Modal>
		)
	}
}


export default PersonEdit;



