import React from 'react';
import {Form, Button, message, Input, Icon, Row, Col, Tag, Popconfirm, Typography, Menu, Dropdown, Modal} from 'antd';
import {showText} from '@/utils/utils';
import moment from 'moment';
import {Base64} from 'js-base64';
import {v4 as uuidv4} from 'uuid';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import MyUpload from "@/components/common/MyUpload";
import ReactAudioPlayer from 'react-audio-player';
import CommentList from "@/components/digitalOrg/CommentList";
import EditeVisit from "@/components/digitalOrg/EditeVisit";
import {inject, observer} from 'mobx-react';

const {Text} = Typography;

@observer
class DetailFormNew extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			addAssignments: [],
			newAddignments: [],
			audioSrc:  "",
			fileName: "",
			isEdit: false,  // 
		}
	}

	componentWillReceiveProps(nextProps) {
		if (JSON.stringify(nextProps.detailData) != JSON.stringify(this.props.detailData)) {
			this.setState({
				newAddignments: nextProps.detailData.addAssignments == null ? [] : nextProps.detailData.addAssignments,
				addAssignments: [],
			});
		}
	}

	componentDidMount() {
		this.props.onRef(this);
	}

	// 
	onAsync = (type, id, index) => {
		this.props.onAsync(type, id, index);
	}

	// 
	remove = (index) => {
		let customList = JSON.parse(JSON.stringify(this.state.addAssignments));
		customList.splice(index, 1);
		this.setState({
			addAssignments: Object.assign([], customList)
		});
	};

	handleAdd = () => {
		let customList = JSON.parse(JSON.stringify(this.state.addAssignments));
		if (customList.some((item) => {
			return item.name == "" || item.name == null;
		})) {
			message.warn("");
			return false;
		}
		if (customList.some((item) => {
			return item.userInformations.length == 0
		})) {
			message.warn("");
			return false;
		}
		this.setState({
			addAssignments: [...customList, {
				"name": "",
				"userInformations": []
			}]
		});
	}

	updateState = (type, data) => {
		this.setState({
			[type]: Object.assign([], data)
		});
	}

	// 
	updatePerson = (index, userIds) => {
		let fields = JSON.parse(JSON.stringify(this.state.addAssignments));
		fields[index].userInformations = Object.assign([], userIds)
		this.setState({
			addAssignments: fields
		});
	}

	onChange = (index, e) => {
		let fields = JSON.parse(JSON.stringify(this.state.addAssignments));
		fields[index].name = e.target.value;
		this.setState({
			addAssignments: fields
		});
	}

	handleSubmit = () => {
		const {addAssignments} = this.state;
		// 
		if (!addAssignments.some((item) => {
			return (item.name == "" || item.name == null) && (item.userInformations.length == 0)
		})) {
			if (addAssignments.some((item) => {
				return item.name == "" || item.name == null;
			})) {
				message.warn("");
				return false;
			}
			if (addAssignments.some((item) => {
				return item.userInformations.length == 0
			})) {
				message.warn("");
				return false;
			}
		}

		/*let {detailData} = this.props;
		let detail = JSON.parse(JSON.stringify(detailData));
		detail.addAssignments = addAssignments.filter((item)=>{
			return !((item.name == "" || item.name == null)&&(item.userInformations.length == 0))
		});*/

		const detail = addAssignments.filter((item)=>{
			return !((item.name == "" || item.name == null)&&(item.userInformations.length == 0))
		}).map(item=>Object.assign(item, {type: 1}))
		this.props.handleAddNew(detail);
	}

	// 
	handleCalc = () => {
		this.setState({
			addAssignments: Object.assign([]),
			audioSrc: "",
			fileName: ""
		});
	}

	clickChange = (e) => {
		const {item} = e.item.props;
		this.setState({
			audioSrc: item.filePath,
			fileName: item.fileName
		});
	}

	// 
	hanndleChangeEdit = () => {
		this.store.changeCanEdit()
		.then((res)=>{
			// , 
			if (res) {
				const {userId, userName} = res;
				const userid = localStorage.getItem("userid");
				if (userId==userid) {
					this.setState({
						isEdit: !this.state.isEdit
					}, ()=>{
						this.store.isEdit = this.state.isEdit;
					});
				} else {
					Modal.warning({
						title: userName+''
					});
				}
			}
		});
	}

	// 
	handleCancle = () => {
		this.setState({
			isEdit: !this.state.isEdit
		}, ()=>{
			this.store.isEdit = this.state.isEdit;
		});
	}

	render() {
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 3},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 20},
			},
		};
		const formItemLayoutWithOutLabel = {
			wrapperCol: {
				xs: {span: 24, offset: 0},
				sm: {span: 20, offset: 0},
			}
		};
		const formItemLayoutLabel = {
			wrapperCol: {
				xs: {span: 24, offset: 0},
				sm: {span: 20, offset: 3},
			}
		};
		let {detailData,visible} = this.props;
		const journalMain = detailData.journalMain || {};
		const journalSub = Base64.decode(detailData.journalSub == undefined ? "" : detailData.journalSub) || {};
		const assignments = detailData.assignments || [];
		const sub = typeof journalSub == "string" ? JSON.parse(journalSub) : {};
		const atts = detailData.atts == null ? [] : detailData.atts;
		const attachments = atts==null?[]:atts.map((vitem, vindex) => {
			return Object.assign({}, {
				uid: vitem.id==null?"!" + uuidv4():vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.id==null?"!" + uuidv4():vitem.id,
				fileName:vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id==null?"!" + uuidv4():vitem.id,
			})
		})
		const audio = detailData.audio==null?[]:detailData.audio;
		const {addAssignments, newAddignments, audioSrc, fileName} = this.state;
		const {isEdit} = this.store;
		const username = localStorage.getItem("username");
		// 
		let isShow = journalMain.userName==username;
		const dropDown = (
			<Dropdown
				overlay={<Menu>
				{audio.map((item,index)=>{
					return <Menu.Item key={index} onClick={this.clickChange} item={item}>{item.fileName}</Menu.Item>
				})}
			</Menu>}>
				<a className="ant-dropdown-link"
				   style={{position: "absolute", right: 0,top: 0 }}
				   onClick={e => e.preventDefault()}>
					{audio.length>0? fileName||audio[0].fileName : null}<Icon type="down" />
				</a>
			</Dropdown>
		);

		const formTailLayout = {
			wrapperCol: { span: 22, offset: 2 },
		};

		return (
			<div className="w pr">
				<div style={{
					cursor: 'pointer',
					position: 'absolute',
					right: '0',
					top: '0',
					zIndex: '5'
				}}>
					{!isEdit&&isShow?<Button className={visible==true?"hidden":""} type="link" onClick={this.hanndleChangeEdit}></Button>:null}
				</div>
				{/**/}
				{!isEdit?
			<Form {...formItemLayoutWithOutLabel}>
				<Form.Item colon={false} className="detail_form_bold">
					<div className="detail_form pr">
						<p className="form_title">{journalMain.title}
							<span style={{
								display: 'inline-block',
								maxWidth: '500px',
								position: 'absolute',
								top: 0,
								paddingLeft: '10px'
							}}>
							{journalMain.tags == null ? null : journalMain.tags.map((item) => {
								return <Tag color="gold" className="ml5">{item.name}</Tag>
							})}
						</span>
						</p>
						<p className="form_name">{journalMain.userName}
							<span
								className="form_time">{showText(moment(journalMain.createTime).format("YYYY-MM-DD HH:mm"), moment(journalMain.createTime).format("HH:mm"))}</span>
							<CommentList
								detailData={detailData}
								store={this.store}
								scrollToAnchor={this.props.scrollToAnchor}
							/>
						</p>
						{audio.length>0&&dropDown}
						<div style={{
							position: 'absolute',
							right: 0,
							top: '30px'
						}}>
							{audioSrc?<ReactAudioPlayer
								src={audioSrc||""}
								controls
							/>:null}
						</div>
					</div>
				</Form.Item>
				<Form.Item label=""
				           colon={false}
				           className="detail_form_bold">
					<span className="detail_form_bold_title"></span><span className="form_nextname">{sub.title}</span>
				</Form.Item>
				<Form.Item label="" colon={false} className="detail_form_bold">
					<span className="detail_form_bold_title"></span><span className="form_nextname">{moment(sub.time).format("YYYYMMDD")}</span>
				</Form.Item>
				<Form.Item label=""
				           colon={false} className="detail_form_bold">
					<span className="detail_form_bold_title"></span>
				</Form.Item>
				<div className="deaft_content_detail" style={{paddingLeft: '14px'}} dangerouslySetInnerHTML={{__html: sub.cases}}></div>
				<Form.Item label=""
				           colon={false} className="detail_form_bold">
					<span className="detail_form_bold_title"></span>
				</Form.Item>
				<div className="deaft_content_detail" style={{paddingLeft: '14px'}} dangerouslySetInnerHTML={{__html: sub.introspection}}></div>
				<Form.Item label="" colon={false} className="detail_form_bold">
					<span className="detail_form_bold_title"></span>
				</Form.Item>
				<Form.Item className="detail_form_bold">
					{assignments.map((item, index) => {
						return <div>
							<span
								className="form_nextname">{index + 1} - {item.name} {item.userInformations == null || item.userInformations.length == 0 ? null : " -- " + item.userInformations.map((vitem) => {
								return vitem.userName
							}).join('')}
								{item.isSync == 1 ? <Text type="warning" className="ml15"></Text> :
									<Button className={!this.props.hasJournal || item.isSync == 1 ? "hidden" : "ml15"}
									        type="link"
									        onClick={this.onAsync.bind(this, "assignments", item.id, index)}></Button>}
							</span>
						</div>
					})}
				</Form.Item>
				<Form.Item label="" colon={false} className="detail_form_bold mt15">
					<span className="detail_form_bold_title"></span>
				</Form.Item>
				<div style={{paddingLeft:'14px'}}>
				<MyUpload
					disabled={true}
					fileList={attachments == null ? [] : attachments}
				/>
				</div>
				{/*, */}
				{!this.props.hasJournal?
					<Form.Item label=""
					           colon={false}
					           className={newAddignments!==undefined&&newAddignments.length==0?"hidden":"detail_form_bold"}>
						<span className="detail_form_bold_title"></span>
						{newAddignments !== undefined && newAddignments.map((item, index) => {
							return <div>
										<span
											className="form_nextname">{index + 1} - {item.name} {item.userInformations == null || item.userInformations.length == 0 ? null : " -- " + item.userInformations.map((vitem) => {
											return vitem.userName
										}).join('')}
											{item.isSync == 1 ? <Text type="warning" className="ml15"></Text> :
												<Button className={!this.props.hasJournal || item.isSync == 1 ? "hidden" : "ml15"}
												        type="link"
												        onClick={this.onAsync.bind(this, "addAssignments", item.id, index)}></Button>}
										</span>
							</div>
						})}
					</Form.Item>:<Form.Item  label=""
					                         colon={false}
					                         className="detail_form_bold">
						<span className="detail_form_bold_title"></span>
						{newAddignments !== undefined && newAddignments.map((item, index) => {
							return <div>
										<span
											className="form_nextname">{index + 1} - {item.name} {item.userInformations == null || item.userInformations.length == 0 ? null : " -- " + item.userInformations.map((vitem) => {
											return vitem.userName
										}).join('')}
											{item.isSync == 1 ? <Text type="warning" className="ml15"></Text> :
												<Button className={!this.props.hasJournal || item.isSync == 1 ? "hidden" : "ml15"}
												        type="link"
												        onClick={this.onAsync.bind(this, "addAssignments", item.id, index)}></Button>}
										</span>
							</div>
						})}
					</Form.Item>}
				{!this.props.hasJournal ? null :<Row className="mb10">
					<Col span={12} className="tc">
						<div></div>
					</Col>
					<Col span={12} className="tc">
						<div></div>
					</Col>
				</Row>}
				{addAssignments !== undefined && addAssignments.map((vitem, vindex) => {
					return <Row key={`subname+${vindex}`}
					            style={{paddingTop: '10px', borderBottom: "1px dashed #E6E6E6"}}>
						<Col span={12} className="pr">
							<Form.Item
								label={" "}
								colon={false}
								labelCol={{
									xs: {span: 24},
									sm: {span: 6},
								}}
								wrapperCol={{
									xs: {span: 24},
									sm: {span: 16}
								}}
							>
								<Input value={vitem.name == null ? "" : vitem.name}
								       placeholder=""
								       onChange={this.onChange.bind(this, vindex)}/>
								<Popconfirm title="?" onConfirm={() => this.remove(vindex)}>
									<Button
										shape="circle"
										className="dynamic-delete-button"
										size="small"
										icon="minus"
										type="default"/>
								</Popconfirm>
							</Form.Item>
						</Col>
						<Col span={12}>
							<Form.Item
								label={" "}
								colon={false}
								labelCol={{
									xs: {span: 24},
									sm: {span: 6}
								}}
								wrapperCol={{
									xs: {span: 24},
									sm: {span: 16}
								}}
							>
								<PersonAddIcon
									data={vitem.userInformations == null ? [] : vitem.userInformations}
									updatePerson={this.updatePerson.bind(this, vindex)}
								/>
							</Form.Item>
						</Col>
					</Row>
				})}
						{!this.props.hasJournal ? null :
							<div>
						<Form.Item {...formItemLayoutLabel}>
							<Icon onClick={this.handleAdd}
							      style={{cursor: 'pointer', margin: '10px 0', color: '#4BA4BE', fontSize: '22px'}}
							      type="plus-circle"/>
						</Form.Item>
						<Form.Item {...formItemLayoutLabel} className={addAssignments !== undefined && addAssignments.length==0?"hidden":"tr"}>
							<Button onClick={this.handleSubmit} type="primary" shape="round">
								
							</Button>
							<Popconfirm placement="top" title="?" onConfirm={() => this.handleCalc()}>
								<Button className="ml15" type="default" shape="round">
									
								</Button>
							</Popconfirm>
						</Form.Item>
					</div>}
			</Form>:<EditeVisit
						onRef={this.onRef}
						store={this.store}
						detailData={detailData}
						onCancle={this.handleCancle}
						pathname={this.props.pathname}
						handleDraftRoute={this.props.handleDraftRoute}
						handleRoute={this.props.handleRoute}
						getTable={this.props.getTable}
						temType="monthlySharing"
					/>}
			</div>
		);
	}
}

export default DetailFormNew;



